.footer {
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19, 19, 19, 0.8);
    color: rgba(255, 255, 255, 1)
}

.footer-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location {
    display: flex;
    margin-bottom: 1rem;
}

.location p {
    line-height: 30px;
}

.right {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;

}

.social p {
    margin-bottom: 1rem;

}

.left a {
    font-size: 1.2rem;
    padding-bottom: 0.7rem;


}

.phone {
    display: flex;
    flex-direction: row;
    padding-bottom: 1rem;

}

.teamviewer {
    display: flex;
    flex-direction: row;
    padding-bottom: 1rem;
}

.email {
    display: flex;
    flex-direction: row;
    padding-bottom: 1rem;


}



h4 {
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p {
    font-size: 1.2rem;
}

@media screen and (max-width: 640px) {
    .footer-container {

        grid-template-columns: 1fr;

    }

}